.page-error {
    margin-bottom: 60px;
    margin-top: 80px;
    display: flex;
    align-items: center;
    flex-direction: row;

    &-outer {
        margin-left: auto;
        margin-right: auto; }

    &_500 {
        width: 1336px;
        margin-top: 150px;
        margin-right: auto;
        margin-left: auto;
        padding: 100px 24px;
        border-radius: 10px;
        border: 1px solid $dark-gray; }

    &-wrap {
        height: 100%; }

    &__text-wrap {
        margin-top: 24px;
        text-align: center; }

    &__img {
        display: block;
        width: 50%;
        flex-shrink: 0;
        margin-right: 24px; }

    &__title {
        font-family: $bold-f;
        max-width: 620px;
        margin-bottom: 36px; }

    &__btn {
        display: inline-block; } }

.maintenance-mode {
    display: flex;
    min-height: 100vh;
    padding: 15px;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    background-image: url(../images/maintenance-mode.jpg);
    background-color: $dark-c;
    background-size: cover;
    background-position: center center;
    color: $light-c;

    &__logo {
        display: block;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 48px;
        width: 380px; }

    &__title {
        font-family: $bold-f;
        font-size: $huge-fz;
        margin-bottom: 24px; }

    &__text-wrap {
        max-width: 500px;
        margin: 0 auto;
        color: $light-c; } }
