// fonts
$base-f: "Proxima Nova Regular", Arial, Helvetica, sans-serif;
$bold-f: "Proxima Nova Bold", Arial, Helvetica, sans-serif;
$additional-f: "SegoeUI", Arial, Helvetica, sans-serif;

$icon-f: "proamursk";

// font-size
$micro-fz: 11px;
$small-fz: 15px;
$mini-fz: 16px;
$base-fz: 18px;
$medium-fz: 22px;
$big-fz: 24px;
$bigger-fz: 30px;
$huge-fz: 36px;

$h6-fz: 14px;
$h5-fz: 18px;
$h4-fz: 20px;
$h3-fz: 24px;
$h2-fz: 30px;
$h1-fz: 36px;

// line-height
$micro-lh: 1;
$mini-lh: 1.3;
$base-lh: 1.5;
$big-lh: 2;

// shadow
$layer-1: 0 2px 4px 0 rgba(0, 0, 0, .07);
$layer-1-hover: 0 4px 8px 0 rgba(0, 0, 0, .1);

$layer-2: 0 2px 6px 0 rgba(0,0,0,0.18);
$layer-2-hover: 0 4px 10px 0 rgba(0,0,0,0.18);

$layer-3: 0 4px 10px 0 rgba(0,0,0,0.18);
$layer-3-hover: 0 6px 14px 0 rgba(0,0,0,0.18);

$layer-4: 0 0px 20px 0 rgba(0, 0, 0, 0.18);

$modal-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.24);

$preloader-time: 1s;

$L-main-grid-gutter: 0.935%; // 800/856
$L-12-grid-gutter: 2.027%; //1200/592
