.btn {
    height: 40px;
    padding: 9px 15px 7px;
    font-family: $bold-f;
    font-size: 16px;
    text-decoration: none;
    border-radius: 20px;

    &_white {
        color: #283348;
        background-color: $light-c; }

    &_blue {
        color: $light-c;
        background: linear-gradient(to right, #0073ff 0%, #00caff 100%); }

    &_more {
        display: block;
        margin-top: 40px;
        margin-bottom: 40px;

        span {
            margin-left: 10px;
            font-size: 0;
            font-size: 12px;
            &::before {} } }

    &_center {
        margin-left: auto;
        margin-right: auto; }

    &_right {
        margin-left: auto; }

    &.disabled,
    &:disabled {
        @extend %disabled; } }
